import { _decorator, Component, Node, Vec3, Vec2, Touch, EventMouse, PhysicsSystem2D, director, BoxCollider2D, Collider2D, Contact2DType } from 'cc';
import ReaveldCard from './ReaveldCard';
const { ccclass, property } = _decorator;

@ccclass('GameManager')
export class GameManager extends Component {

   @property(Node)
   stack1: Node
   @property(Node)
   stack2: Node
   @property(Node)
   stack3: Node
   @property(Node)
   stack4: Node
   @property(Node)
   stack5: Node
   @property(Node)
   stack6: Node
   @property(Node)
   stack7: Node

   onLaod() {
      PhysicsSystem2D.instance.enable = true;
      let coll = this.stack1.getComponent(Collider2D);
      coll.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
   }
   
   onBeginContact(self: Collider2D, other: Collider2D) {
      console.log("self", self.tag);
      console.log("other", other.tag);
      // let selfcollider = event.selfCollider;
      // let otherCollider = event.otherCollider;
      // console.log("self", selfcollider);
      // console.log("other", otherCollider);
   }

}

